<template>
  <nav>
    <v-app-bar app elevate-on-scroll>
      <v-toolbar-title>
        <router-link to="/" flat>
          <span class="blue--text text--lighten-1">Blog</span>
        </router-link>
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-menu bottom offset-y left>
        <template v-slot:activator="{ on }">
          <v-btn dark icon v-on="on" class="d-flex d-sm-none">
            <v-icon>mdi-menu</v-icon>
          </v-btn>
        </template>

        <v-list v-if="!isAuthenticated">
          <v-list-item v-for="(item, i) in menuNotActive" :key="i" :to="item.section">
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
        <v-list v-if="isAuthenticated">
          <v-list-item to="/newpost">
            <v-list-item-title>Create Post</v-list-item-title>
          </v-list-item>
          <v-list-item @click="logOut()">
            <v-list-item-title>Logout</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-toolbar-items class="d-none d-sm-flex" v-if="!isAuthenticated">
        <v-btn v-for="(item, i) in menuNotActive" :key="i" :to="item.section" text>{{ item.title }}</v-btn>
      </v-toolbar-items>
      <v-toolbar-title v-if="isAuthenticated">Hello, {{ userLogged.username }}</v-toolbar-title>
      <v-toolbar-items class="d-none d-sm-flex" v-if="isAuthenticated">
        <!--<v-btn
          v-for="(item, i) in menuActive"
          :key="i"
          @click="logOut()"
          text
        >{{item.title}}</v-btn>-->

        <v-btn to="/newpost" text>Create Post</v-btn>
        <v-btn @click="logOut()" text>Logout</v-btn>
      </v-toolbar-items>
    </v-app-bar>
  </nav>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      drawer: false,
      menuNotActive: [
        { icon: "home", title: "Login", section: "/login" },
        { icon: "info", title: "Register", section: "/register" }
      ],
      menuActive: [
        { icon: "plus", title: "Create Post" },
        { icon: "warning", title: "Logout" }
      ],
      options: {
        duration: 500,
        offset: 0,
        easing: "easeInOutCubic"
      }
    };
  },
  methods: {
    menuItems() {
      return this.menu;
    },
    logOut() {
      this.$store.dispatch("logout");
      this.$router.push("/login");
    }
  },
  computed: mapGetters(["isAuthenticated", "userLogged"])
};
</script>
