<template>
  <div class="home">
    <v-container>
      <v-row justify="center">
        <v-col lg="6">
          <v-row v-for="(post, index) in allPosts" :key="index">
            <v-col>
              <v-card>
                <v-card-title class="headline">
                  <router-link :to="{name: 'Post', params:{postId:post._id}}">{{post.title}}</router-link>
                </v-card-title>
                <v-img :src="'./' + post.image"></v-img>
                <v-card-subtitle v-text="post.description"></v-card-subtitle>
                <v-card-actions>
                  <v-btn color="orange" @click="redirect(post._id)" text>
                    {{post.comments.length}}
                    <v-icon right dark>mdi-comment</v-icon>
                  </v-btn>
                  <v-btn
                    color="orange"
                    @click="upvote(post._id)"
                    text
                    v-if="post.voters.indexOf(userLogged.id)===-1"
                  >
                    {{post.voters.length}}
                    <v-icon right dark>mdi-arrow-up-bold-box</v-icon>
                  </v-btn>
                  <v-btn color="orange" @click="downvote(post._id)" text v-else>
                    {{post.voters.length}}
                    <v-icon right dark>mdi-arrow-down-bold-box</v-icon>
                  </v-btn>
                  <v-btn
                    color="orange"
                    @click="remove(post._id)"
                    text
                    v-if="post.author == userLogged.id"
                  >
                    <v-icon dark>mdi-trash-can-outline</v-icon>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Home",
  data() {
    return {};
  },
  computed: mapGetters(["isAuthenticated", "allPosts", "userLogged"]),
  methods: {
    ...mapActions(["fetchPosts", "upvotePost", "downvotePost", "deletePost"]),
    upvote(id) {
      this.upvotePost(id);
    },
    downvote(id) {
      this.downvotePost(id);
    },
    remove(id) {
      this.deletePost(id).then(() => {
        console.log("deleted");
      });
    },
    redirect(id) {
      this.$router.push({ name: "Post", params: { postId: id } });
    }
  },
  created() {
    this.fetchPosts();
  }
};
</script>
